* {
  margin: 0;
}

.column {
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
  flex-basis: 33.33%;
  min-width: 300px;
  flex-grow: 1;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: #fff;
}

.block--1 {
  background-color: rgb(255, 0, 0);
  order: 1;
}

.block--2 {
  background-color: rgb(204, 0, 0);
  order: 2;
}

.block--3 {
  background-color: rgb(153, 0, 0);
  order: 3;
}

.block--4 {
  background-color: rgb(102, 0, 0);
  order: 6;
}

.block--5 {
  background-color: rgb(51, 0, 0);
  order: 5;
}

.block--6 {
  background-color: rgb(0, 0, 0);
  order: 4;
}

@media screen and (max-width:899px) {

  .block--3 {
    order: 4;
  }

  .block--4 {
    order: 3;
  }

  .block--6 {
    order: 6;
  }
}

@media screen and (max-width:599px) {
  .block--3 {
    order: 3;
  }

  .block--4 {
    order: 4;
  }

  .block--6 {
    order: 6;
  }
}
